.App-logo {
  animation: spin infinite 20s linear;
  height: 70px;
  margin: 10px 0 0 0;
}

.App-header {
  background-color: rgb(0, 177, 24);
  padding: 20px;
  color: white;
  text-align: center;
  border-radius: 10px 10px 0 0;
  box-shadow: 0px 5px 8px 3px rgb(189, 189, 189);
}

@media (min-width: 769px) {
  .App-header {
    background-color: rgb(0, 177, 24);
    padding: 20px;
    color: white;
    text-align: center;
    border-radius: 0 0 0 0;
    box-shadow: 0px 0px 0px 0px rgb(189, 189, 189);
  }
}

.App-intro {
  font-size: large;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
