body {
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans KR', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  margin: 20px;
  padding: 0px;
  background: #eee;
  font-family: 'Noto Sans KR', sans-serif;
}

@media (min-width: 769px) {
  body {
    margin: 0px;
    padding: 0px;
    background: #eee;
    font-family: 'Noto Sans KR', sans-serif;
  }
}

.container {
  position: relative;
  max-width: 810px;
  margin: 0 auto;
  background: #fff;
  border-radius: 0 0 10px 10px;
  box-shadow: 0px 5px 8px 3px rgb(189, 189, 189);
}

@media (min-width: 769px) {
  .container {
    margin: 2.5rem auto;
    border-radius: 10px 10px 10px 10px;
    box-shadow: 0px 5px 8px 3px rgb(189, 189, 189);
  }
}

/* 체크: Result Component */
.result {
  padding: 1.5rem 2.5rem;
}

/* 수정 필요: Quiz Component */
.answerOptions {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* 체크: QuestionCount Component */
.questionCount {
  padding: 1.5rem 2.5rem 0;
  font-size: 14px;
}

/* 체크: Question Component */
.question {
  margin: 0;
  padding: 0.5rem 2.5rem 1.5rem 2.5rem;
}

/* 체크: AnswerOption Component */
.answerOption {
  border-top: 1px solid #eee;
}

.answerOption:hover {
  background-color: #eefef4;
}

.radioCustomButton {
  position: absolute;
  width: auto;
  opacity: 0;
}

.radioCustomButton,
.radioCustomLabel {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}

.radioCustomLabel {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 1.5rem 2.5rem 1.5rem 5rem;
  font-size: 16px;
  line-height: 1.5;
}

.radioCustomButton ~ .radioCustomLabel:before {
  position: absolute;
  top: 20px;
  left: 38px;
  width: 28px;
  height: 28px;
  content: '';
  display: inline-block;
  vertical-align: middle;
  background: #fff;
  border: 1px solid #bbb;
  border-radius: 50%;
  transition: all 0.3s;
}

.radioCustomButton:checked ~ .radioCustomLabel:before {
  content: '';
  background: #8bc53f url(/quiz-show/static/media/icon-check.4cffa739.svg) no-repeat;
  background-size: 27px;
  border-color: #8bc53f;
}

/* 애니메이션 */
.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 0.5s ease-in-out 0.3s;
}

.fade-leave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 1;
}

.fade-leave.fade-leave-active {
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.fade-appear {
  opacity: 0;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.App-logo {
  -webkit-animation: spin infinite 20s linear;
          animation: spin infinite 20s linear;
  height: 70px;
  margin: 10px 0 0 0;
}

.App-header {
  background-color: rgb(0, 177, 24);
  padding: 20px;
  color: white;
  text-align: center;
  border-radius: 10px 10px 0 0;
  box-shadow: 0px 5px 8px 3px rgb(189, 189, 189);
}

@media (min-width: 769px) {
  .App-header {
    background-color: rgb(0, 177, 24);
    padding: 20px;
    color: white;
    text-align: center;
    border-radius: 0 0 0 0;
    box-shadow: 0px 0px 0px 0px rgb(189, 189, 189);
  }
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.box_stage {
  width: 100%;
  height: 100%;
}

.title {
  margin-top: 100px;
  text-align: center;
  font-weight: 300;
  font-size: 34px;
  color: rgb(236, 236, 236);
}

#intro_wrapper {
  position: absolute;
  top: 45%;
  left: 50%;
  border: solid 1px rgb(1, 83, 45);
  width: 310px;
  height: 440px;
  margin-left: -155px;
  margin-top: -220px;
  background-color: rgb(28, 168, 0);
  border-radius: 10px;
  box-shadow: 0px 6px 8px 4px rgb(123, 196, 230);
}

#intro_button {
  display: flex;
  height: 80%;
  justify-content: center;
  align-items: center;
}

#intro_button a {
  text-decoration: none;
  text-align: center;
  font-weight: 700;
  color: white;
  padding: 10px;
  width: 100px;
  background-color: black;
  border: 2px solid rgb(110, 245, 128);
  border-radius: 10px;
}
